import axios from "axios";
import { useEffect, useRef, useState } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useCart } from "./CartContext";
import Select from "react-select";
import LocationSelect from "./LocationSelect";

const ServicesPageLayer = () => {
  // UI State
  const [activeButton, setActiveButton] = useState("grid-view");
  const [filter, setFilter] = useState(false);
  const [activeTab, setActiveTab] = useState("services");
  const [showInfoModal, setShowInfoModal] = useState(false);
  const [selectedNote, setSelectedNote] = useState("");
  const [servicesLoading, setServicesLoading] = useState(false);

  // Data State
  const [services, setServices] = useState([]);
  const [packages, setPackages] = useState([]);
  const [categories, setCategories] = useState([]);
  const [getAllcategories, setGetAllCategories] = useState([]);
  const [getAllservices, setGetAllServices] = useState([]);
  const [centerStats, setCenterStats] = useState([]);
  const [places, setPlaces] = useState([]);
  // const [selected, setSelected] = useState(null);

  // Filter State
  const [query, setQuery] = useState("");
  const [address, setAddress] = useState("");
  const [centreType, setCentreType] = useState("");
  const [healthInsurance, setHealthInsurance] = useState(false);
  const [cashless, setCashless] = useState(false);
  const [sortPrice, setSortPrice] = useState("");
  const [selectedCategory, setSelectedCategory] = useState({
    _id: "",
    name: "",
  });
  const [selectedService, setSelectedService] = useState("");
  const [filterByLocation, setFilterByLocation] = useState("");
  const [selectedLocality, setSelectedLocality] = useState("");
  // Pagination State
  const [currentPage, setCurrentPage] = useState(1);
  const servicesPerPage = 9;

  // Category State
  const [activeCategory, setActiveCategory] = useState("ALL");

  // Messages State
  const [noServicesMsg, setNoServicesMsg] = useState("");
  const [noPackagesMsg, setNoPackagesMsg] = useState("");
  const [isSearching, setIsSearching] = useState(false);

  // Hooks
  const { addToCart, cartItems } = useCart();
  const navigate = useNavigate();
  const location = useLocation();

  // Pagination Logic
  const indexOfLastService = currentPage * servicesPerPage;
  const indexOfFirstService = indexOfLastService - servicesPerPage;
  const currentServices = services.slice(
    indexOfFirstService,
    indexOfLastService,
  );
  const totalServicePages = Math.ceil(services.length / servicesPerPage);

  // API Functions
  const fetchServices = async () => {
    try {
      setServicesLoading(true);
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/centreService/`,
      );
      setServices(res.data);
    } catch (error) {
      console.error("Error fetching services", error.message);
    } finally {
      setServicesLoading(false);
    }
  };

  const fetchPackages = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/packages/`,
      );
      setPackages(res.data);
    } catch (error) {
      console.error("Error fetching packages", error.message);
    }
  };

  const fetchServicesByCategory = async (category) => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/centreService/by-category/${category}`,
      );
      setServices(res.data);
    } catch (err) {
      console.error("Error fetching services:", err);
    }
  };

  const fetchAllServices = async () => {
    try {
      const response = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/getAllServices`,
      );
      setGetAllServices(response.data.services);
    } catch (err) {
      console.error("Error fetching all services:", err);
    }
  };

  const fetchCenterStats = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/centres/centreTypeStats`,
      );
      console.log("center stats", res.data);

      setCenterStats(res.data);
    } catch (error) {
      console.error("Failed to get center stats", error);
    }
  };

  const fetchCategories = async () => {
    try {
      const [categoriesRes, allCategoriesRes] = await Promise.all([
        axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/category/getCategoriesAndCount`,
        ),
        axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/category/getAllCategories`,
        ),
      ]);

      setCategories(categoriesRes.data);
      setGetAllCategories(allCategoriesRes.data);
    } catch (err) {
      console.error("Error fetching categories", err);
    }
  };

  const fetchLocalityByDistrict = async (district) => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/places/by-district`,
        {
          params: { district: district },
        },
      );
      console.log("Locality by district:", res.data);
      setPlaces(res.data.offices || []);
    } catch (error) {
      console.error("Failed to get Locality by district", error);
      setPlaces([]);
    }
  };

  // Fetch localities when district changes
  useEffect(() => {
    if (filterByLocation) {
      fetchLocalityByDistrict(filterByLocation);
    } else {
      setPlaces([]); // Clear localities when no district is selected
    }
  }, [filterByLocation]);

  //  const fetchAllLocality = async () => {
  //   try {
  //     const res = await axios.get(
  //       `${process.env.REACT_APP_BACKEND_URL}/api/places/all`
  //     );
  //     console.log("Locality",res.data);

  //     setPlaces(res.data.offices);
  //   } catch (error) {
  //     console.error("Failed to get Locality", error);
  //   }
  // };

  // Search and Filter Functions
  const handleSearch = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/centres/searchService&Package`,
        {
          params: {
            name: query,
            address,
            centreType,
            healthInsurance: healthInsurance ? "true" : undefined,
            cashless: cashless ? "true" : undefined,
            sortByPrice: sortPrice,
            filterByCategory: selectedCategory.name,
            filterByService: selectedService,
            filterByLocation,
            filterByPlace: selectedLocality,
          },
        },
      );

      setServices(res.data.services || []);
      setPackages(res.data.packages || []);
      setNoServicesMsg(
        res.data.services?.length === 0 ? "No services found." : "",
      );
      setNoPackagesMsg(
        res.data.packages?.length === 0 ? "No packages found." : "",
      );
    } catch (err) {
      console.error("Search failed", err);
      setNoServicesMsg("No services found.");
      setNoPackagesMsg("No packages found.");
    }
  };

  // Search debounce effect
  useEffect(() => {
    const delayDebounce = setTimeout(() => {
      if (
        query.trim() === "" &&
        address.trim() === "" &&
        centreType.trim() === "" &&
        !healthInsurance &&
        !cashless &&
        !sortPrice &&
        !selectedCategory.name &&
        !selectedService &&
        !filterByLocation &&
        !selectedLocality
      ) {
        if (activeCategory && activeCategory !== "ALL") {
          fetchServicesByCategory(activeCategory);
        } else {
          fetchServices();
        }
        fetchPackages();
      } else {
        setActiveCategory("ALL");
        handleSearch();
      }
    }, 500);

    return () => clearTimeout(delayDebounce);
  }, [
    query,
    address,
    centreType,
    healthInsurance,
    cashless,
    sortPrice,
    selectedCategory,
    selectedService,
    activeCategory,
    filterByLocation, // âœ… include this
    selectedLocality,
  ]);

  const clearAllFilters = () => {
    setSelectedService("");
    setSelectedCategory("");
    setSortPrice("");
    setCentreType("");
    setQuery("");
    setFilterByLocation(null);
    setSelectedLocality("");
    setCurrentPage(1);
    fetchServices();
  };

  const handleCategoryChange = async (e) => {
    const selectedOption = e.target.options[e.target.selectedIndex];
    const _id = selectedOption.dataset.id;
    const name = selectedOption.value;

    setSelectedCategory({ _id, name });
    setSelectedService("");

    if (_id) {
      try {
        const response = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/${_id}`,
        );
        const servicesData = response.data?.services || [];
        setGetAllServices(servicesData);

        if (servicesData.length === 0) {
          setServices([]);
        }
      } catch (err) {
        console.error("Error fetching services:", err);
        setGetAllServices([]);
        setServices([]);
      }
    } else {
      fetchAllServices();
    }
  };

  // Cart Functions
  const isPackageInCart = (pkgId) => {
    return cartItems.some(
      (item) => item.type === "package" && item._id === pkgId,
    );
  };

  const handleBookNow = async (serviceId, fromPage) => {
    try {
      const response = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/centreService/single/${serviceId}`,
      );
      const selectedService = response.data;
      addToCart(selectedService);
      navigate("/cart", { state: { from: fromPage } });
    } catch (error) {
      console.error("Error adding to cart", error);
    }
  };

  const handleBookNowPackage = async (packageId, fromPage) => {
    if (isPackageInCart(packageId)) {
      navigate("/cart", { state: { from: fromPage } });
      return;
    }

    try {
      const response = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/packages/single/${packageId}`,
      );
      const selectedPackage = { ...response.data, type: "package" };
      addToCart(selectedPackage);
      navigate("/cart", { state: { from: fromPage } });
    } catch (error) {
      console.error("Error adding package to cart", error);
    }
  };

  // UI Handlers
  const handleClick = (buttonName) => setActiveButton(buttonName);
  const handleFilter = () => setFilter(!filter);
  const handlePageChange = (pageNumber) => setCurrentPage(pageNumber);

  const handleInfoClick = (note) => {
    setSelectedNote(note || "No additional instructions provided.");
    setShowInfoModal(true);
  };

  const handleCloseInfoModal = () => {
    setShowInfoModal(false);
    setSelectedNote("");
  };

  const handlePageChangeWithScroll = (page) => {
    handlePageChange(page); // your existing page change logic
    window.scrollTo({ top: 0, behavior: "smooth" }); // scroll to top smoothly
  };

  // Pagination Component
  const renderPagination = () => {
    const pages = [];
    const maxVisible = 4;
    let startPage = Math.max(1, currentPage - Math.floor(maxVisible / 2));
    let endPage = startPage + maxVisible - 1;

    if (endPage > totalServicePages) {
      endPage = totalServicePages;
      startPage = Math.max(1, endPage - maxVisible + 1);
    }

    // first button
    pages.push(
      <li
        key="first"
        className={`page-item ${currentPage === 1 ? " disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => currentPage !== 1 && handlePageChangeWithScroll(1)}
        >
          First
        </button>
      </li>,
    );

    // Prev Button
    pages.push(
      <li
        key="prev"
        className={`page-item ${currentPage === 1 ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() =>
            currentPage > 1 && handlePageChangeWithScroll(currentPage - 1)
          }
        >
          Prev
        </button>
      </li>,
    );

    // Page Numbers
    for (let i = startPage; i <= endPage; i++) {
      pages.push(
        <li
          key={i}
          className={`page-item ${currentPage === i ? "active" : ""}`}
        >
          <button
            className="page-link"
            onClick={() => handlePageChangeWithScroll(i)}
          >
            {i}
          </button>
        </li>,
      );
    }

    // Next Button
    pages.push(
      <li
        key="next"
        className={`page-item ${
          currentPage === totalServicePages ? "disabled" : ""
        }`}
      >
        <button
          className="page-link"
          onClick={() =>
            currentPage < totalServicePages &&
            handlePageChangeWithScroll(currentPage + 1)
          }
        >
          Next
        </button>
      </li>,
    );

    pages.push(
      <li
        key="last"
        className={`page-item ${
          currentPage === totalServicePages ? "disabled" : ""
        }`}
      >
        <button
          className="page-link"
          onClick={() => handlePageChangeWithScroll(totalServicePages)}
        >
          Last
        </button>
      </li>,
    );

    return pages;
  };

  // Utility Functions
  const toTitleCase = (str) => {
    return str
      .toLowerCase()
      .split(" ")
      .map((word) => word.charAt(0).toUpperCase() + word.slice(1))
      .join(" ");
  };

  // Effects
  useEffect(() => {
    fetchCategories();
    fetchAllServices();
    fetchCenterStats();
    // fetchAllLocality();
  }, []);

  // Handle URL category parameter
  useEffect(() => {
    const params = new URLSearchParams(location.search);
    const categoryFromUrl = params.get("category");

    if (categoryFromUrl) {
      const formattedCategory = toTitleCase(categoryFromUrl);

      const matchingCategory = getAllcategories.find((cat) =>
        cat.category_name.toLowerCase().includes(categoryFromUrl.toLowerCase()),
      );

      if (matchingCategory) {
        setSelectedCategory({
          _id: matchingCategory._id,
          name: matchingCategory.category_name,
        });

        fetchServicesByCategory(matchingCategory.category_name);

        axios
          .get(
            `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/${matchingCategory._id}`,
          )
          .then((res) => setGetAllServices(res.data.services || []))
          .catch((err) =>
            console.error("Error fetching services for URL category:", err),
          );
      }
    } else {
      setSelectedCategory({ _id: "", name: "" });
      setActiveCategory("ALL");
      fetchServices();
    }

    fetchPackages();
  }, [location.search, getAllcategories]);

  const modalRef = useRef(null);

  useEffect(() => {
    // Handle click outside
    const handleClickOutside = (event) => {
      if (
        modalRef.current &&
        !modalRef.current.contains(event.target) &&
        showInfoModal
      ) {
        showInfoModal(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    // ðŸ”‘ Scroll lock logic
    if (showInfoModal) {
      document.body.style.overflow = "hidden"; // Disable background scroll
    } else {
      document.body.style.overflow = "auto"; // Enable background scroll
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
      document.body.style.overflow = "auto"; // Cleanup when component unmounts
    };
  }, [showInfoModal]);

  // Add this useEffect to reset pagination whenever filters change
  useEffect(() => {
    setCurrentPage(1);
  }, [
    query,
    address,
    centreType,
    healthInsurance,
    cashless,
    sortPrice,
    selectedCategory,
    selectedService,
    activeCategory,
    filterByLocation,
    selectedLocality,
  ]);

  // Also reset pagination in handleCategoryClick function
  const handleCategoryClick = (category) => {
    setActiveCategory(category);
    setCurrentPage(1); // Add this line

    if (category === "ALL") {
      fetchServices();
    } else {
      fetchServicesByCategory(category);
    }

    // Reset filters
    setQuery("");
    setAddress("");
    setCentreType("");
    setHealthInsurance(false);
    setCashless(false);
    setSortPrice("");
    setSelectedCategory({ _id: "", name: "" });
    setSelectedService("");
  };

  // Also add reset in handleCategoryChange

  // const handleCategoryChange = async (e) => {
  //   const selectedOption = e.target.options[e.target.selectedIndex];
  //   const _id = selectedOption.dataset.id;
  //   const name = selectedOption.value;

  //   setSelectedCategory({ _id, name });
  //   setSelectedService("");
  //   setCurrentPage(1); // Add this line

  //   if (_id) {
  //     try {
  //       const response = await axios.get(
  //         `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/${_id}`
  //       );
  //       setGetAllServices(response.data?.services || []);
  //     } catch (err) {
  //       console.error("Error fetching services:", err);
  //     }
  //   } else {
  //     fetchAllServices();
  //   }
  // };

  return (
    <section
      className={`all-product padding-y-120 ${
        activeButton === "list-view" ? "list-view" : ""
      }`}
      style={{ marginTop: "-30px" }}
    >
      <div className="container container-two">
        <div className="row">
          <div className="col-lg-12">
            {/* Search and Filter Bar */}
            <div className="filter-tab d-flex justify-content-between align-items-center flex-wrap gap-3">
              <form
                onSubmit={(e) => e.preventDefault()}
                className="search-box d-flex align-items-center"
                style={{ width: "410px" }}
              >
                <input
                  type="text"
                  className="common-input common-input--lg pill shadow-sm boder-2"
                  placeholder="Search services . . ."
                  value={query}
                  onChange={(e) => setQuery(e.target.value)}
                  style={{
                    borderTopRightRadius: "0px",
                    borderBottomRightRadius: "0px",
                  }}
                />
                <button
                  type="button"
                  style={{
                    backgroundColor: "#000424",
                    height: "47px",
                    border: "none",
                    borderRadius: "0 999px 999px 0",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    padding: "0 16px",
                    marginLeft: "-42px",
                    cursor: "pointer",
                  }}
                  onClick={() => handleSearch()}
                >
                  <img
                    src="assets/images/icons/search.svg"
                    alt="Search"
                    style={{ width: "20px", height: "20px" }}
                  />
                </button>
              </form>

              {/* <div className="d-flex align-items-center gap-3 flex-wrap justify-content-end ms-auto">
                <ul
                  className="nav common-tab nav-pills mb-0 gap-lg-2 gap-1"
                  id="pills-tab"
                  role="tablist"
                >
                  <li className="nav-item" role="presentation">
                    <div
                      className={`nav-link ${
                        activeTab === "services" ? "active" : ""
                      }`}
                      id="pills-services-tab"
                      // data-bs-toggle="pill"
                      // data-bs-target="#pills-services"
                      // type="button"
                      role="tab"
                      aria-controls="pills-services"
                      aria-selected={
                        activeTab === "services" ? "true" : "false"
                      }
                    >
                      All Services
                    </div>
                  </li>
                </ul>
              </div> */}
            </div>

            {/* Filter Form */}
            <form action="#" className="filter-form pb-4 d-block">
              <div className="row gy-3">
                <div className="col-sm-3 col-xs-6">
                  <div className="flx-between gap-1">
                    <label
                      htmlFor="selectedCategory"
                      className="form-label font-16"
                    >
                      Filter by Category
                    </label>
                  </div>
                  <div className="position-relative">
                    <select
                      id="selectedCategory"
                      className="common-input border-gray-five"
                      value={selectedCategory.name || ""}
                      onChange={handleCategoryChange}
                    >
                      <option value="" data-id="">
                        Select a Category
                      </option>
                      {getAllcategories.map((cat) => (
                        <option
                          key={cat._id}
                          value={cat.category_name}
                          data-id={cat._id}
                        >
                          {cat.category_name}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>

                <div className="col-sm-3 col-xs-6">
                  <div className="flx-between gap-1">
                    <label
                      htmlFor="selectedServices"
                      className="form-label font-16"
                    >
                      Filter by Services
                    </label>
                  </div>
                  <div className="position-relative">
                    <Select
                      options={
                        getAllservices.length > 0
                          ? getAllservices.map((srv) => ({
                              value: srv.serviceName,
                              label: srv.serviceName,
                            }))
                          : [
                              {
                                value: "",
                                label: "No services available",
                                isDisabled: true,
                              },
                            ]
                      }
                      value={
                        selectedService
                          ? { value: selectedService, label: selectedService }
                          : null
                      }
                      onChange={(selectedOption) =>
                        setSelectedService(selectedOption?.value || "")
                      }
                      placeholder={
                        getAllservices.length > 0
                          ? "Select a Service"
                          : "No services available"
                      }
                      isClearable={getAllservices.length > 0}
                      isDisabled={getAllservices.length === 0}
                      menuPortalTarget={document.body}
                      styles={{
                        control: (base, state) => ({
                          ...base,
                          border: "1px solid #dcdcdc",
                          borderRadius: "4px",
                          minHeight: "58px",
                          boxShadow: "none",
                          "&:hover": { borderColor: "#dcdcdc" },
                          backgroundColor: state.isDisabled
                            ? "#f8f9fa"
                            : "#fff",
                        }),
                        valueContainer: (base) => ({
                          ...base,
                          padding: "0 8px",
                        }),
                        placeholder: (base) => ({
                          ...base,
                          color:
                            getAllservices.length === 0
                              ? "#6c757d"
                              : "#6c757d !important",
                          fontSize: "16px",
                          fontWeight: "400",
                          opacity: 1,
                        }),
                        menuPortal: (base) => ({ ...base, zIndex: 9999 }),
                        menuList: (base) => ({
                          ...base,
                          maxHeight: "250px",
                          overflowY: "auto",
                        }),
                        indicatorSeparator: () => ({ display: "none" }),
                        option: (base, state) => ({
                          ...base,
                          backgroundColor: state.isDisabled
                            ? "#f8f9fa"
                            : base.backgroundColor,
                          color: state.isDisabled ? "#6c757d" : base.color,
                          cursor: state.isDisabled ? "not-allowed" : "pointer",
                        }),
                      }}
                    />
                  </div>
                </div>

                <div className="col-sm-2 col-xs-6">
                  <div className="flx-between gap-1">
                    <label htmlFor="sortPrice" className="form-label font-16">
                      Sort by Price
                    </label>
                  </div>
                  <div className="position-relative">
                    <select
                      id="sortPrice"
                      className="common-input border-gray-five"
                      value={sortPrice}
                      onChange={(e) => setSortPrice(e.target.value)}
                    >
                      <option value="">Select</option>
                      <option value="asc">Lowest to Highest</option>
                      <option value="desc">Highest to Lowest</option>
                    </select>
                  </div>
                </div>

                <LocationSelect
                  value={filterByLocation}
                  onChange={setFilterByLocation}
                  colClass="col-sm-2"
                />

                <div className="col-sm-2 col-xs-6">
                  <div className="flx-between gap-1">
                    <label
                      htmlFor="sortLocality"
                      className="form-label font-16"
                    >
                      Sort by Locality
                    </label>
                  </div>
                  <div className="position-relative">
                    <Select
                      options={
                        places.length > 0
                          ? places.map((place) => ({
                              value: place.officename,
                              label: place.officename,
                            }))
                          : [
                              {
                                value: "",
                                label: "No Places available",
                                isDisabled: true,
                              },
                            ]
                      }
                      value={
                        selectedLocality
                          ? { value: selectedLocality, label: selectedLocality }
                          : null
                      }
                      onChange={(selectedOption) =>
                        setSelectedLocality(selectedOption?.value || "")
                      }
                      placeholder={
                        places.length > 0 ? "Select a locality" : "No Locality"
                      }
                      isClearable={places.length > 0}
                      isDisabled={places.length === 0}
                      menuPortalTarget={document.body}
                      styles={{
                        control: (base, state) => ({
                          ...base,
                          border: "1px solid #dcdcdc",
                          borderRadius: "4px",
                          minHeight: "58px",
                          boxShadow: "none",
                          "&:hover": { borderColor: "#dcdcdc" },
                          backgroundColor: state.isDisabled
                            ? "#f8f9fa"
                            : "#fff",
                        }),
                        valueContainer: (base) => ({
                          ...base,
                          padding: "0 8px",
                        }),
                        placeholder: (base) => ({
                          ...base,
                          color:
                            places.length === 0
                              ? "#6c757d"
                              : "#6c757d !important",
                          fontSize: "16px",
                          fontWeight: "400",
                          opacity: 1,
                        }),
                        menuPortal: (base) => ({ ...base, zIndex: 9999 }),
                        menuList: (base) => ({
                          ...base,
                          maxHeight: "250px",
                          overflowY: "auto",
                        }),
                        indicatorSeparator: () => ({ display: "none" }),
                        option: (base, state) => ({
                          ...base,
                          backgroundColor: state.isDisabled
                            ? "#f8f9fa"
                            : base.backgroundColor,
                          color: state.isDisabled ? "#6c757d" : base.color,
                          cursor: state.isDisabled ? "not-allowed" : "pointer",
                        }),
                      }}
                    />
                  </div>
                </div>

                <div className="col-12 mobile-centre-type-dropdown">
                  <div className="flx-between gap-1">
                    <label
                      htmlFor="mobileCentreType"
                      className="form-label font-16"
                    >
                      Healthcare Centre Type
                    </label>
                  </div>
                  <div className="position-relative">
                    <select
                      id="mobileCentreType"
                      className="common-input border-gray-five"
                      value={centreType}
                      onChange={(e) => {
                        setCentreType(e.target.value);
                        handleSearch();
                      }}
                    >
                      <option value="">All Healthcare Centres</option>
                      {centerStats?.map((centre, index) => (
                        <option key={index} value={centre.centreType}>
                          {centre.centreType}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
              </div>
            </form>
          </div>

          <div className="col-xl-3 col-lg-4">
            {/* Filter Sidebar */}
            {activeTab === "services" && (
              <div
                className={`filter-sidebar ${filter ? "show" : ""}`}
                style={{ marginBottom: "20px" }}
              >
                <button
                  type="button"
                  className="filter-sidebar__close p-2 position-absolute end-0 top-0 z-index-1 text-body hover-text-main font-20 d-lg-none d-block"
                  onClick={handleFilter}
                >
                </button>

                <div className="filter-sidebar__item">
                  <button
                    type="button"
                    className="filter-sidebar__button font-16 text-capitalize fw-500"
                  >
                    Healthcare Centre Type
                  </button>

                  <div className="filter-sidebar__content">
                    <ul className="filter-sidebar-list">
                      <li className="filter-sidebar-list__item">
                        <button
                          className={`filter-sidebar-list__text ${
                            centreType === "" ? "active-category" : ""
                          }`}
                          onClick={() => {
                            setCentreType("");
                            handleSearch();
                          }}
                        >
                          All Healthcare Centres
                        </button>
                      </li>

                      {centerStats?.map((centre, index) => (
                        <li key={index} className="filter-sidebar-list__item">
                          <button
                            className={`filter-sidebar-list__text ${
                              centreType === centre.centreType
                                ? "active-category"
                                : ""
                            }`}
                            onClick={(e) => {
                              e.preventDefault();
                              setCentreType(centre.centreType);
                              handleSearch();
                            }}
                          >
                            {centre.centreType}
                          </button>
                        </li>
                      ))}
                    </ul>
                  </div>
                </div>
              </div>
            )}
          </div>

          {/* Main Content */}
          <div className="col-xl-9 col-lg-8">
            <div className="tab-content" id="pills-tabContent">
              <div
                className="tab-pane fade show active"
                id="pills-services"
                role="tabpanel"
                aria-labelledby="pills-services-tab"
                tabIndex={0}
              >
                <div className="row gy-4 list-grid-wrapper">
                  {servicesLoading ? (
                    // Loading State
                    <div className="col-12">
                      <div className="text-center py-5">
                        <div
                          className="spinner-border text-primary"
                          role="status"
                        >
                          <span className="visually-hidden">
                            Loading services...
                          </span>
                        </div>
                        <p className="mt-2">Loading services...</p>
                      </div>
                    </div>
                  ) : currentServices.length > 0 ? (
                    currentServices.map((service) => (
                      <div
                        className="col-xl-4 col-lg-4 col-sm-6"
                        key={service._id}
                      >
                        <div
                          className="product-item h-90 d-flex flex-column overflow-hidden"
                          style={{ boxShadow: "0 0 10px rgba(0, 0, 0, 0.1)" }}
                        >
                          <div
                            className="product-item__thumb"
                            style={{ height: "180px", flexShrink: 0 }}
                          >
                            <Link
                              to={`/service-details/${service._id}`}
                              className="link w-100"
                            >
                              <img
                                src={service?.centre?.logo}
                                alt=""
                                className="cover-img w-100"
                                style={{ height: "180px", objectFit: "fill" }}
                              />
                            </Link>
                          </div>

                          <div className="product-item__content d-flex flex-column p-3 flex-grow-1">
                            <div
                              style={{
                                display: "flex",
                                alignItems: "center",
                                gap: "8px",
                              }}
                            >
                              <h6 className="product-item__title mb-0">
                                <Link
                                  to={`/service-details/${service._id}`}
                                  className="link"
                                >
                                  {toTitleCase(service.serviceName)}
                                </Link>
                              </h6>

                              {service.serviceCategory ===
                                "OPERATIVE PROCEDURES" && (
                                <span
                                  title={
                                    service.note ||
                                    "No additional instructions provided."
                                  }
                                  style={{
                                    cursor: "pointer",
                                    color: "rgb(13, 110, 253)",
                                    marginBottom: "15px",
                                  }}
                                  onClick={() => handleInfoClick(service.note)}
                                >
                                  <i className="fas fa-info-circle"></i>
                                </span>
                              )}
                            </div>

                            <div className="product-item__info d-flex justify-content-between align-items-center mb-3">
                              <span
                                className="product-item__author text-muted small"
                                style={{
                                  display: "-webkit-box",
                                  WebkitLineClamp: 2, // limit to 2 lines
                                  WebkitBoxOrient: "vertical",
                                  overflow: "hidden",
                                  textOverflow: "ellipsis",
                                  maxWidth: "60%", // adjust based on layout
                                  height: "2.9em",
                                }}
                              >
                                <Link
                                  to={`/profile/${service.centre._id}`}
                                  className="link hover-text-decoration-underline"
                                >
                                  {toTitleCase(service.centre?.centreName)}
                                </Link>
                              </span>

                              <div className="d-flex align-items-center gap-1 flex-shrink-0">
                                <h6
                                  className="product-item__price mb-0 text-success fw-bold"
                                  style={{ lineHeight: "1", margin: 0 }}
                                >
                                  â‚¹{service.totalPrice}
                                </h6>
                                {service.discountPrice && (
                                  <span
                                    className="product-item__prevPrice text-muted text-decoration-line-through"
                                    style={{
                                      lineHeight: "1",
                                      fontSize: "0.9rem",
                                    }}
                                  >
                                    â‚¹{service.actualPrice}
                                  </span>
                                )}
                              </div>
                            </div>

                            {service?.doctors?.length > 0 && (
                              <div
                                className="d-flex align-items-center gap-2"
                                style={{
                                  marginBottom: "-5px",
                                  maxHeight: "60px",
                                }}
                              >
                                <img
                                  src={
                                    service.doctors[0].profilePhoto ||
                                    "/assets/images/default-doctor.png"
                                  }
                                  alt={service.doctors[0].name || "Doctor"}
                                  style={{
                                    width: "35px",
                                    height: "35px",

                                    borderRadius: "50%",
                                    objectFit: "fill",
                                  }}
                                />

                                <div
                                  className="small"
                                  style={{ height: "72px" }}
                                >
                                  <span className="fw-semibold d-block">
                                    {service.doctors[0].name || "Dr. Name"}
                                  </span>

                                  <div
                                    style={{
                                      display: "-webkit-box",
                                      WebkitLineClamp: 2, // show max 3 lines
                                      WebkitBoxOrient: "vertical",
                                      overflow: "hidden",
                                      textOverflow: "ellipsis",
                                      maxHeight: "2.8em",
                                      lineHeight: "1.4em", // ensures consistent line spacing
                                    }}
                                    className="d-flex flex-column"
                                  >
                                    <span className="text-muted">
                                      {service.doctors[0].specialization
                                        ? toTitleCase(
                                            service.doctors[0].specialization,
                                          )
                                        : "Specialization"}
                                      ,{" "}
                                    </span>
                                    <span className="text-muted">
                                      {service.doctors[0].qualification ||
                                        "Qualification"}
                                    </span>
                                  </div>

                                  {service.doctors.length > 1 && (
                                    <Link
                                      to={`/service-details/${service._id}`}
                                      className="text-primary small"
                                    >
                                      + {service.doctors.length - 1} more
                                    </Link>
                                  )}
                                </div>
                              </div>
                            )}

                            <div className="mt-4 d-flex gap-2">
                              <Link
                                to={`/service-details/${service._id}`}
                                className="btn btn-outline-light btn-sm pill w-100"
                              >
                                More Details
                              </Link>
                              <Link
                                to="/cart"
                                state={{ from: "/all-service" }}
                                className="btn btn-outline-light btn-sm pill w-100"
                                onClick={() =>
                                  handleBookNow(service._id, "/all-service")
                                }
                              >
                                Book Now
                              </Link>
                            </div>
                          </div>
                        </div>
                      </div>
                    ))
                  ) : (
                    <div className="col-12 text-center py-5">
                      {/* <img 
                            src="/assets/images/empty-state.svg" 
                            alt="No centres" 
                            width="200" 
                            className="mb-3"
                          /> */}
                      <h5 className="mt-3 text-muted">No Services found</h5>
                      <p className="text-muted">
                        Try adjusting your search or filters
                      </p>
                      <button
                        className="btn btn-main mt-2"
                        onClick={clearAllFilters}
                      >
                        Clear All Filters
                      </button>
                    </div>
                  )}
                </div>

                {/* Pagination */}
                {currentServices.length > 0 && totalServicePages > 1 && (
                  <nav aria-label="Page navigation example">
                    <ul className="pagination common-pagination justify-content-center">
                      {renderPagination()}
                    </ul>
                  </nav>
                )}
              </div>
            </div>
          </div>
        </div>
        <style>{`
        
        `}</style>

        {/* Info Modal */}
        {showInfoModal && (
          <div
            style={{
              position: "fixed",
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              backgroundColor: "rgba(0,0,0,0.5)",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              zIndex: 1000,
            }}
          >
            <div
              style={{
                backgroundColor: "#fff",
                borderRadius: "10px",
                maxWidth: "600px",
                width: "95%",
                maxHeight: "95vh",
                boxShadow: "0 0 10px rgba(0,0,0,0.3)",
                position: "relative",
                overflowY: "auto",
              }}
            >
              {/* Modal Header */}
              <div
                style={{
                  backgroundColor: "#3a3a3a",
                  color: "#fff",
                  padding: "15px 20px",
                  fontSize: "18px",
                  fontWeight: "bold",
                  position: "relative",
                }}
              >
                Notes for Patients
                <button
                  onClick={handleCloseInfoModal}
                  style={{
                    position: "absolute",
                    top: "10px",
                    right: "10px",
                    background: "transparent",
                    border: "none",
                    fontSize: "30px",
                    color: "#fff",
                    cursor: "pointer",
                  }}
                >
                  &times;
                </button>
              </div>

              {/* Modal Body */}
              <div
                style={{
                  padding: "20px",
                  color: "#333",
                  display: "flex",
                  flexDirection: "column",
                  gap: "10px",
                }}
              >
                <p>{selectedNote}</p>
              </div>
            </div>
          </div>
        )}
      </div>
      <style>{`/* Show mobile dropdown only on screens less than 990px */
.mobile-centre-type-dropdown {
  display: none;
}

@media (max-width: 989px) {
  .mobile-centre-type-dropdown {
    display: block;
  }
}

/* Hide desktop sidebar on screens less than 990px */
@media (max-width: 989px) {
  .filter-sidebar {
    display: none !important;
  }
}`}</style>
    </section>
  );
};

export default ServicesPageLayer;
